import React, {Component} from 'react'

import './index.css'

import Header from '../../components/header'
import Content from './content'

class DetailPage extends Component {
  // 展示搜索页
  showSearchPage () {
    this.props.showSearchPage()
  }

  render () {
    const detailPageInfo = this.props.detailPageInfo // 详情页面信息内容
    const activeSearchPage = this.props.activeSearchPage // 获取详情页对应的活动页名称
    const pageState = this.props.pageState // 获取主页显示页面状态

    return (
      <div>
        {/* Detailpage组件 内含Header 私有Content */}
        <Header
          activeSearchPage={activeSearchPage}
          showSearchPage={this.showSearchPage.bind(this)}
          searchPageScrollY={this.props.searchPageScrollY}
        />

        <Content
          activeSearchPage={activeSearchPage}
          detailPageInfo={detailPageInfo}
          pageState={pageState}
        />
      </div>
    )
  }
}

export default DetailPage
