import React, {Component} from 'react'
import LazyLoad from 'react-lazyload'
import CONFIG from '../../../../CONFIG'
import {getPageInfo} from '../../../util/util'

import './index.css'

class Item extends Component {
  // 点击事件, 展示详情页
  handleClick (item) {
    this.props.showDetailPage(item)
  }

  render () {
    const activePage = getPageInfo(this.props.activePage).pageName // 获取活动页名称
    const item = this.props.item // 获取列表item的信息
    // 通用
    const img = item.image || item.images.small // 获取图片
    const title = item.title || '无相关标题信息' // 获取title
    const tags = item.tags || [] // 获取tags
    const rating = item.rating.average || '无相关评分信息' // rating

    // 相应的页面 配置
    switch (activePage) {
      case CONFIG.page[0].pageName:
        const authorArr = item.author.length ? item.author : ['无相关作者信息']
        const author = authorArr.length > 1 ? `${authorArr[0]} 等` : authorArr[0]
        const pubdate = item.pubdate || '无相关出版时间信息'

        return (
          <div
            className='item-container--book'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='封面' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>名称: {title}</span>

              <div className='item-info-tags'>
                {tags.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item.name}
                      </span>
                    )
                  }
                })
                }
              </div>

              <span className='item-info-author'>作者: {author}</span>

              <span className='item-info-rating'>评分: {rating}</span>

              <span className='item-info-rating'>时间: {pubdate}</span>
            </div>
          </div>
        )

      case CONFIG.page[1].pageName:
        const genres = item.genres || [] // 获取genres
        const casts = item.casts || [] // 获取演员数组

        return (
          <div
            className='item-container--movie'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='封面' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>名称: {title}</span>

              <div className='item-info-tags'>
                {genres.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item}
                      </span>
                    )
                  }
                })}
              </div>

              <div className='item-info-casts'>
                {casts.map((item, index) => {
                  return (
                    <span className='casts-item' key={index}>
                      {item.name}
                    </span>
                  )
                })}
              </div>

              <span className='item-info-rating'>评分: {rating}</span>
            </div>
          </div>
        )

      case CONFIG.page[2].pageName:
        const authorMusic = item.author ? item.author[0] : {name: '无相关作者信息'}

        return (
          <div
            className='item-container--music'
            onClick={this.handleClick.bind(this, item)}
          >

            <div className='item-img-wrapper'>
              <LazyLoad height={'100%'}>
                <img src={img} alt='封面' className='item-img' />
              </LazyLoad>
            </div>

            <div className='item-info-wrapper'>
              <span className='item-info-tt'>名称: {title}</span>

              <div className='item-info-tags'>
                {tags.map((item, index) => {
                  if (index < 3) {
                    return (
                      <span className='tags-item' key={index}>
                        {item.name}
                      </span>
                    )
                  }
                })
                }
              </div>

              <span className='item-info-author'>作者: {authorMusic.name}</span>

              <span className='item-info-rating'>评分: {rating}</span>
            </div>
          </div>
        )

      default:
        return (
          <div>没有相关的页面配置哦~~</div>
        )
    }
  }
}

export default Item
