import React, { Component } from 'react'
import CONFIG from '../../../../CONFIG'
import {getActivePageName} from '../../../util/util'

import './index.css'

import Item from './item'

class Footer extends Component {
  constructor () {
    super()
    this.state = {
      activeItem: getActivePageName() // 当前被选中的item
    }
  }

  // 设置当前被选中的item
  setActiveItem (item) {
    this.setState({
      activeItem: item
    })
  }

  // 切换footer item时需要定位至相应的滚动条位置
  setScrollY () {
    const key = this.state.activeItem
    const scrollY = this.props.scrollY

    window.scrollTo(0, scrollY[key])
  }

  render () {
    const setPage = this.props.setPage // 设置页面跳转

    const page = CONFIG.page // 获取searchPage中所有的page
    const activeItem = this.state.activeItem // 当前被选中的item
    const setActiveItem = this.setActiveItem // 设置当前被选中的item

    return (
      <div className='footer'>
        {/* Footer组件 内含私有Item组件 */}
        {page.map((item, index) => {
          return (
            <Item
              key={index}
              page={item}
              activeItem={activeItem}
              setActiveItem={setActiveItem.bind(this)}
              setPage={setPage.bind(this)}
              setScrollY={this.setScrollY.bind(this)}
            />
          )
        })}
      </div>
    )
  }
}

export default Footer
