import React, { Component } from 'react'
import {getPageInfo} from '../../../util/util'

import './index.css'

class Header extends Component {
  // 点击事件, 展示其对应的搜索页面
  handleClick () {
    const key = this.props.activeSearchPage
    const searchPageScrollY = this.props.searchPageScrollY

    this.props.showSearchPage()

    window.setTimeout(() => { window.scrollTo(0, searchPageScrollY[key]) }, 0)
  }

  render () {
    const activeSearchPage = this.props.activeSearchPage // 获取详情页对应的搜索页

    const detailPageInfo = getPageInfo(activeSearchPage).detailPage // 获取详情页信息
    const returnBtnTxt = detailPageInfo.returnBtnTxt // 获取头部返回按钮文本
    const headerText = detailPageInfo.headerText // 获取头部文本

    return (
      <div className='header'>
        {/* Header组件 */}
        <div
          onClick={this.handleClick.bind(this)}
          className='header-btn'
        >
          <i className='iconfont icon-back' />
          <span>{returnBtnTxt}</span>
        </div>
        <div className='header-text'>{headerText}</div>
      </div>
    )
  }
}

export default Header
