import React, { Component } from 'react'
import {getActivePageName} from './util/util'

import './lib/reset.css'
import './lib/fonts/iconfont.css'
import './style.css'

import SearchPage from './component/page/search-page'
import DetailPage from './component/page/detail-page'

class App extends Component {
  constructor () {
    super()
    this.state = {
      page: 'searchPage', // 主页展示的页面: searchPage ? detailPage ? or more...
      searchPage: getActivePageName(), // searchPage 展示的活动页: 图书? 电影? 音乐? or more...
      detailPageInfo: {}, // detailPage展示的详情信息
      searchPageScrollY: {} // 获取searchPageScrollY
    }
  }

  // 设置搜索页面展示的页面
  setActiveSearchPage (page) {
    this.setState({
      searchPage: page
    })
  }

  // 展示详情页面
  showDetailPage (item) {
    this.setState({
      page: 'detailPage', // 主页展示detailPage
      detailPageInfo: item // detailPage详情信息
    })
    window.scrollTo(0, 0)
  }

  // 展示搜索页面
  showSearchPage () {
    this.setState({
      page: 'searchPage' // 主页展示searchPage
    })
  }

  // 获取searchPage的scrollY
  getSearchPageScrollY (scrolly) {
    this.setState({
      searchPageScrollY: scrolly
    })
  }

  render () {
    // 搜索页与详情页间的切换
    const showPage = this.state.page

    const showSearchPage = showPage === 'searchPage'
    const showDetailPage = showPage === 'detailPage'

    const searchPageCls = showSearchPage ? '' : 'searchPage-hidden'
    const detailPageCls = showDetailPage ? '' : 'detailPage-hidden'

    // 详情页面信息
    const detailPageInfo = this.state.detailPageInfo

    return (
      <div className='app'>
        {/* 这里把App划分成两大组件: SearchPage 和 DetailPage */}
        {/* SearchPage */}
        <div className={searchPageCls}>
          <SearchPage
            activeSearchPage={this.state.searchPage}
            setActiveSearchPage={this.setActiveSearchPage.bind(this)}
            showDetailPage={this.showDetailPage.bind(this)}
            getSearchPageScrollY={this.getSearchPageScrollY.bind(this)}
          />
        </div>

        {/* DeatailPage */}
        <div className={detailPageCls}>
          <DetailPage
            detailPageInfo={detailPageInfo}
            activeSearchPage={this.state.searchPage}
            showSearchPage={this.showSearchPage.bind(this)}
            searchPageScrollY={this.state.searchPageScrollY}
            pageState={this.state.page}
          />
        </div>
      </div>
    )
  }
}

export default App
