import React, { Component } from 'react'
import {getPlaceholder, getPageInfo, getJson} from '../../../util/util'

import './index.css'

class Search extends Component {
  constructor () {
    super()
    this.state = {
      searchText: '' // 搜索文本
    }
  }

  // 键盘KeyUp事件, 监听并改变搜索文本, 若监听到Enter则调用searchEvent
  handleKeyUpEvent (e) {
    const searchText = e.target.value
    const keyCode = e.keyCode

    // 这里让我十分苦恼, 点击时state中的searchText总是少获取一次keyup事件, 所以这里不得不用了一下异步的形式来保持e.target.value与state.searchText保持同步
    window.setTimeout(() => {
      this.setState({
        searchText: searchText
      })

      if (keyCode === 13) {
        const searchType = this.props.searchType // 获取搜索类型
        const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 获取默认搜索文本数组
        const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 随机获取默认搜索内容
        const searchText = this.state.searchText || rdmDefaultSearch // 获取搜索文本, 为空时则搜索默认文本

        this.searchEvent(searchType, searchText, 0)
      }
    }, 0)
  }

  // 点击监听事件, 若点击则提交包含着对应搜索信息的json中的数组
  searchEvent (searchType, searchText, start) {
    this.props.onSubmitSearchText(this.state.searchText)

    getJson(searchType, searchText, start, json => {
      const searchResultArrName = getPageInfo(searchType).searchPage.searchResultArrName
      this.props.onSubmit(json[searchResultArrName])
    })
  }

  render () {
    const searchType = this.props.searchType // 获取搜索类型

    const placeholder = getPlaceholder(searchType) // 获取placeholder
    const defaultSearchArr = getPageInfo(searchType).searchPage.defaultSearch // 获取默认搜索文本数组
    const rdmDefaultSearch = defaultSearchArr[Math.floor(Math.random() * defaultSearchArr.length)] // 随机获取默认搜索内容
    const searchText = this.state.searchText || rdmDefaultSearch // 获取搜索文本, 为空时则搜索默认文本

    return (
      <div className='search-wrapper'>
        {/* Search组件 搜索框 */}
        <input
          type='text'
          className='search-input'
          placeholder={placeholder}
          onKeyUp={this.handleKeyUpEvent.bind(this)}
        />

        <i className='iconfont icon-search' />

        <input
          type='button'
          value='搜索'
          className='search-btn'
          onClick={this.searchEvent.bind(this, searchType, searchText, 0)}
        />
      </div>
    )
  }
}

export default Search
