import React, {Component} from 'react'
import LazyLoad from 'react-lazyload'
import CONFIG from '../../../../CONFIG'

import './index.css'

class Content extends Component {
  render () {
    const activeSearchPage = this.props.activeSearchPage // 对应的searchpage活动页
    const detailPageInfo = this.props.detailPageInfo // 详情页信息对象
    const pageState = this.props.pageState // 页面状态 识别当前页面为searchpage 还是 detailpage

    // 如果传入的是一个空对象 或 现在页面的状态不是detailpage 则显示 正在加载 页面
    if (!Object.keys(detailPageInfo).length || pageState !== 'detailPage') {
      return (
        <div>{CONFIG.pageLoading.text}</div>
      )
    }

    // 公共属性
    const img = detailPageInfo.image || detailPageInfo.images.small // img
    const nullImg = CONFIG.nullImg // 若图片不存在则启用设置中的备用图片
    const title = detailPageInfo.title || '无相关名称信息' // tt
    const rating = detailPageInfo.rating.average || '无相关评分信息' // rating
    const tags = detailPageInfo.tags || [] // tags

    // 相应的页面 配置
    switch (activeSearchPage) {
      case CONFIG.page[0].pageName:
        const authorArr = detailPageInfo.author.length ? detailPageInfo.author : ['无相关作者信息']
        const publisher = detailPageInfo.publisher || '无出版社信息'
        const pubdate = detailPageInfo.pubdate || '无相关出版时间信息'
        const price = detailPageInfo.price || '无相关价格信息'
        const catalog = detailPageInfo.catalog || '无相关序言信息'
        const summary = detailPageInfo.summary || '无相关简介信息'

        return (
          <div className='content--book'>

            <div className='content-abstract-container'>
              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='封面' className='abstract-img' />
                </LazyLoad>
              </div>

              <div className='info-wrapper'>
                <div className='info-tt'>名称: {title}</div>

                <div>
                  <span className='info-author'>作者: </span>
                  {authorArr.map((item, index) => {
                    return (
                      <span key={index}>item</span>
                    )
                  })}
                </div>

                <div className='info-publisher'>出版社: {publisher}</div>

                <div className='info-pubdate'>时间: {pubdate}</div>

                <div className='info-rating'>评分: {rating}</div>

                <div className='info-price'>价钱: {price}</div>

                <div className='info-tags'>
                  {tags.map((item, index) => {
                    if (index < 3) {
                      return (
                        <span className='tags-item' key={index}>
                          {item.name}
                        </span>
                      )
                    }
                  })
                  }
                </div>
              </div>
            </div>

            <div className='content-detail-container'>

              <div className='detail-catalog'>
                <h1 className='detail-tt'>序言</h1>
                <p className='detail-p'>{catalog}</p>
              </div>

              <div className='detail-summary'>
                <h1 className='detail-tt'>简介</h1>
                <p className='detail-p'>{summary}</p>
              </div>
            </div>
          </div>
        )

      case CONFIG.page[1].pageName:
        const genres = detailPageInfo.genres || []
        const year = detailPageInfo.year || '无相关上映年份信息'
        const directorsArr = detailPageInfo.directors.length ? detailPageInfo.directors : [{name: '无相关导演信息'}]
        const originalTitle = detailPageInfo.original_title || ''
        const casts = detailPageInfo.casts.length ? detailPageInfo.casts : [{avatars: {small: nullImg.src}}]

        return (
          <div className='content--movie'>

            <div className='content-abstract-container'>

              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='封面' className='abstract-img' />
                </LazyLoad>
              </div>
            </div>

            <div className='content-detail-container'>

              <div className='detail-summary'>

                <h1 className='detail-tt'>简介</h1>

                <div className='info-tt'>
                  <span>名称: {title} </span>
                  <span className='info-tags'>
                    {genres.map((item, index) => {
                      if (index < 3) {
                        return (
                          <span className='tags-item' key={index}>
                            {item}
                          </span>
                        )
                      }
                    })}
                  </span>
                </div>

                <div className='info-year'>上映时间: {year}</div>

                <div className='info-directors-wrapper'>
                  <span>导演: </span>
                  {directorsArr.map((item, index) => {
                    return (
                      <span key={index} className='info-directors'>{`${item.name}  `}</span>
                    )
                  })}
                </div>

                <div className='info-original-tt'>{`${title}(${originalTitle})`}</div>
              </div>

              <div className='detail-casts-container'>

                <h1 className='detail-tt'>演员</h1>
                <div className='detail-casts-avartar-container'>
                  {casts.map((item, index) => {
                    return (
                      <div key={index} className='casts-avatar-wrapper'>
                        <LazyLoad height={'100%'}>
                          <img src={item.avatars ? item.avatars.small : nullImg.src} alt='演员' className='casts-avatar' />
                        </LazyLoad>
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
          </div>
        )

      case CONFIG.page[2].pageName:
        const authorMusic = detailPageInfo.author ? detailPageInfo.author[0] : {name: '无相关作者信息'}
        const publisherMusic = detailPageInfo.attrs.publisher ? detailPageInfo.attrs.publisher[0] : '无相关发布商信息'
        const pubdateMusic = detailPageInfo.attrs.pubdate ? detailPageInfo.attrs.pubdate[0] : '无相关发布时间信息'
        const tracksArr = detailPageInfo.attrs.tracks ? detailPageInfo.attrs.tracks : ['无相关专辑信息']

        return (
          <div className='content--music'>
            <div className='content-abstract-container'>
              <div className='img-wrapper'>
                <LazyLoad height={'100%'}>
                  <img src={img} alt='封面' className='abstract-img' />
                </LazyLoad>
              </div>

              <div className='info-wrapper'>
                <div className='info-tt'>
                  <span>名称: {title} </span>
                  <span className='info-tags'>
                    {tags.map((item, index) => {
                      return (
                        <span className='tags-item' key={index}>
                          {item.name}
                        </span>
                      )
                    })
                    }
                  </span>
                </div>

                <div className='info-author'>作者: {authorMusic.name}</div>

                <div className='info-publisher'>发布商: {publisherMusic}</div>

                <div className='info-pubdate'>发布时间: {pubdateMusic}</div>

                <div className='info-rating'>评分: {rating}</div>
              </div>
            </div>

            <div className='content-detail-container'>
              <div className='detail-summary'>
                <h1 className='detail-tt'>简介</h1>
              </div>

              <div className='detail-tracks'>
                <h1 className='detail-tt'>专辑</h1>
                {tracksArr.map((item, index) => {
                  return (
                    <div key={index} className='tracks-item'>{item}</div>
                  )
                })}
              </div>
            </div>

          </div>
        )

      default:
        return (
          <div>wrong page...</div>
        )
    }
  }
}

export default Content
