import React, { Component } from 'react'

import './index.css'

class Item extends Component {
  // 点击事件: 更改当前被选中的footer选项卡, 跳转至相应的搜索页面
  handleClick (item, page) {
    this.props.setActiveItem(item)
    this.props.setPage(page)
    window.setTimeout(() => { this.props.setScrollY() }, 0) // 异步处理 为了保证先改变页面状态 再设置滚动条位置
  }

  render () {
    const item = this.props.page.pageName // 页脚item文本
    const icon = this.props.page.icon // 对应的icon
    const activeItem = this.props.activeItem // 接收当前被选中的item

    const page = item // 页面
    const isActive = item === activeItem // 判断该item是否被选中
    const activeCls = isActive ? 'active-item' : '' // 设置相应样式

    return (
      <div
        className={'footer-item ' + activeCls}
        onClick={this.handleClick.bind(this, item, page)}
      >
        {/* footer-item */}
        <i className={'footer-item-icon iconfont icon-' + icon} />

        <div className='footer-item-text'>{item}</div>
      </div>
    )
  }
}

export default Item
