import React, { Component } from 'react'
import CONFIG from '../../../../CONFIG'
import {getActivePageName} from '../../../util/util'

import './index.css'

import Item from './item'

class List extends Component {
  // 显示详情页
  showDetailPage (item) {
    this.props.showDetailPage(item)
  }

  render () {
    const activePage = this.props.page // 当前活动页
    const arr = this.props.arr[getActivePageName(activePage)] // 包含当前活动页列表信息的数组

    const pageLoadingText = CONFIG.pageLoading.text // 页面加载中文本
    const noSearchResource = CONFIG.noSearchResource.text // 无相关搜索记录文本

    // if 信息数组为undefined 显示正在加载页面 else if 信息数组长度为0 则显示 无相关搜索记录页面
    if (arr === undefined) {
      return (
        <div className='loading-wrapper'>
          <div className='loading'>{pageLoadingText}</div>
        </div>
      )
    } else if (!arr.length) {
      return (
        <div className='none-wrapper'>
          <div className='none'>{noSearchResource}</div>
        </div>
      )
    }

    return (
      <div className='list'>
        {/* List组件 内含私有Item组件 */}
        {arr.map((item, index) => {
          return (
            <Item
              item={item}
              key={index}
              showDetailPage={this.showDetailPage.bind(this)}
              activePage={activePage}
            />
          )
        })}
      </div>
    )
  }
}

export default List
